.featured {
    margin-top: 2rem;
    margin-bottom: 2rem;
}
.featured a:link {
    text-decoration: none;
}
.featured-img {
    max-height: 10rem;
}
.featured-name {
    text-align: center;
    margin-top: .75rem;
}
.new-recipe {
    width: 100%;
    margin-bottom: 2rem;
}
.new-image {
    width: 100%;
    vertical-align: baseline;
    height: 4rem;
}
img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.recipe-name {
    font-size: 1em;
}
.display-inline {
    display: inline-block;
}

@media (min-width: 335px) {
    .new-image {
        vertical-align: middle;
    }
}